<template>
  <div class="container-packageOperation">
    <headerUsually btnShow="true" titleText="套餐菜操作" url="/mobileOrdering/SDishOperation"></headerUsually>
    <div class="container-nav">
      <div class="content-nav">
        <p class="item">
          <span class="item-span">套餐代码：{{packaged.code}}</span>
          <span class="item-span">套餐名称：{{packaged.descript}}</span>
        </p>
        <p class="item">
          <span class="item-span">套餐金额：{{packaged.amount}}</span>
          <span class="item-span">
            套餐明细金额：
            <span class="amount">{{Number(packaged.totalAmount).toFixed(2)}}</span>
          </span>
        </p>
      </div>
    </div>
    <div class="container-main">
      <div class="content-above content">
        <div class="title">
          <div class="title-left"></div>
          <div class="title-text">套餐明细</div>
          <div class="title-right"></div>
        </div>
        <div class="list">
          <div
            class="item"
            :class="item.selected?'activeItem':''"
            v-for="(item, index) in upperArr"
            :key="index"
            @click="upperSelectedFun(item,index)"
          >
            <div class="item-left">{{item.descript}}</div>
            <div class="item-right iconfont icon-xuanzhong" v-if="item.selected"></div>
          </div>
          <!-- <div class="item activeItem">
            <div class="item-left">dsaf</div>
            <div class="item-right iconfont icon-xuanzhong"></div>
          </div>
          -->
        </div>
        <div class="btn" @click="turnOutFun()">转出套餐</div>
      </div>
      <div class="content-below content">
        <div class="title">
          <div class="title-left"></div>
          <div class="title-text">非套餐明细</div>
          <div class="title-right"></div>
        </div>
        <div class="list">
          <div
            class="item"
            :class="item.selected?'activeItem':''"
            v-for="(item, index) in downArr"
            :key="index"
            @click="downSelectedFun(item,index)"
          >
            <div class="item-left">{{item.descript}}</div>
            <div class="item-right iconfont icon-xuanzhong" v-if="item.selected"></div>
          </div>
          <!-- <div class="item">
            <div class="item-left">sdafasd</div>
            <div class="item-right iconfont icon-xuanzhong"></div>
          </div>-->
        </div>
        <div class="btn" @click="turnIn()">转入套餐</div>
      </div>
    </div>
  </div>
</template>
<script>
import ajaxGet from "@/conf/ajaxGet";
import { Toast, MessageBox } from "mint-ui";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
import { mapState, mapGetters } from "vuex";
export default {
  components: {
    headerUsually
  },
  data() {
    return {
      upperArr: [],
      downArr: [],
      upperIndexArr: [],
      downIndexArr: [],
      newModifySDish: {},
      newMyOrderInfo: {},
      packaged: {
        code: "",
        descript: "",
        amount: "",
        totalAmount: 0
      }
    };
  },
  computed: {
    ...mapState({
      modifySDish: state => state.mobileOrdering.modifySDish,
      myOrderInfo: state => state.mobileOrdering.myOrderInfo
    })
  },
  created() {
    console.log("modifySDish:", this.modifySDish);
    console.log("myOrderInfo:", this.myOrderInfo);
    this.newModifySDish = _.cloneDeep(this.modifySDish);
    this.newMyOrderInfo = _.cloneDeep(this.myOrderInfo);
    this.packaged.code = this.newModifySDish.id;
    this.packaged.descript = this.newModifySDish.descript;
    this.packaged.amount =
      this.newModifySDish.amount / this.newModifySDish.number;

    if (this.newModifySDish.accnt) {
      this.newMyOrderInfo.orders.forEach(element => {
        if (element.inumber == this.newModifySDish.tnumber) {
          this.$set(element, "selected", false);
          this.packaged.totalAmount += Number(element.price * element.number);
          this.upperArr.push(element);
        }
        if (
          element.inumber == 0 &&
          element.flag.indexOf("1") != 0 &&
          element.pluState == this.newModifySDish.pluState
        ) {
          this.$set(element, "selected", false);
          this.downArr.push(element);
        }
      });
    } else {
      this.newModifySDish.pluStds.forEach(ele => {
        this.$set(ele, "selected", false);
        this.$set(ele, "pccode", this.newModifySDish.pccode);
        ele.descript = ele.pluDescript ? ele.pluDescript : ele.descript;
        this.packaged.totalAmount += Number(element.price * element.number);
        this.upperArr.push(ele);
      });
      this.newMyOrderInfo.orders.forEach(ele => {
        if (ele.flag.indexOf("1") != 0) {
          this.$set(ele, "selected", false);
          this.downArr.push(ele);
        }
      });
    }
    console.log("this.downArr:", this.downArr);
    console.log("this.upperArr:", this.upperArr);
  },
  methods: {
    async turnOutFun() {
      if (this.upperIndexArr.length == 0) {
        return;
      }
      let changItemArr = [];
      let ids = "";
      this.upperArr.forEach(ele => {
        if (ele.selected) {
          ele.selected = false;
          ids += ele.id + ",";
          ele.inumber = 0;

          ele.price = ele.price0
            ? Number.parseFloat(ele.price0).toFixed(2)
            : ele.price;
          ele.amount = ele.amount
            ? (ele.amount / ele.number) * Number.parseFloat(ele.number)
            : Number.parseFloat(ele.price) * Number.parseFloat(ele.number);

          ele.number = this.newModifySDish.number;
          ele.pluState = 1;
          this.packaged.totalAmount -= ele.amount;
          changItemArr.push(ele);
        }
      });
      if (this.newModifySDish.accnt) {
        let data = {
          params: {
            accnt: this.newModifySDish.accnt,
            pluState: this.newModifySDish.pluState,
            orderId: this.newModifySDish.id,
            ids: ids,
            directionMode: "out"
          }
        };

        let res = await ajaxGet("/pos/router?method=comealOutIn", data);
        if (res && res.resultCode == 0) {
          this.downArr.push(...changItemArr);

          this.upperIndexArr.sort(function(x, y) {
            return y - x;
          });

          // console.log("this.upperIndexArr:", this.upperIndexArr);
          this.upperIndexArr.forEach(ele => {
            // console.log("ele:", ele);
            this.upperArr[ele].inumber = 0;
            this.newMyOrderInfo.orders.forEach(element => {
              if (element.id == this.upperArr[ele].id) {
                Object.assign(element, this.upperArr[ele]);
              }
            });
            this.upperArr.splice(ele, 1);
          });

          this.$store.commit("submiMyOrderInfo", this.newMyOrderInfo);
          // console.log("this.newMyOrderInfo.orders:", this.newMyOrderInfo);
          this.upperIndexArr = [];
        }
      } else {
        this.downArr.push(...changItemArr);
        this.upperIndexArr.sort(function(x, y) {
          return y - x;
        });
        // console.log("this.upperIndexArr:", this.upperIndexArr);
        this.upperIndexArr.forEach(ele => {
          this.upperArr.splice(ele, 1);
        });
        this.newMyOrderInfo.orders.forEach(element => {
          if (
            element.pccode == this.newModifySDish.pccode &&
            element.sortCode == this.newModifySDish.sortCode &&
            element.pluCode == this.newModifySDish.pluCode
          ) {
            this.upperIndexArr.forEach(ele => {
              element.pluStds.splice(ele, 1);
            });
          }
        });
        this.upperIndexArr.forEach(ele => {
          this.newModifySDish.pluStds.splice(ele, 1);
        });
        this.newMyOrderInfo.orders.push(...changItemArr);
        this.upperIndexArr = [];
        // console.log(this.newMyOrderInfo.orders);
        this.$store.commit("changeTempTable", {
          type: "turnOut",
          item: this.newMyOrderInfo.orders
        });
        this.$store.commit("submiMyOrderInfo", this.newMyOrderInfo);
        this.$store.commit("submitModifySDish", this.newModifySDish);
      }
    },
    async turnIn() {
      if (this.downIndexArr.length == 0) {
        return;
      }
      let changItemArr = [];
      let ids = "";
      this.downArr.forEach(ele => {
        if (ele.selected) {
          ele.selected = false;
          ids += ele.id + ",";
          this.packaged.totalAmount += ele.amount;
          changItemArr.push(ele);
        }
      });
      if (this.newModifySDish.accnt) {
        let data = {
          params: {
            accnt: this.newModifySDish.accnt,
            pluState: this.newModifySDish.pluState,
            orderId: this.newModifySDish.id,
            ids: ids,
            directionMode: "in"
          }
        };

        let res = await ajaxGet("/pos/router?method=comealOutIn", data);
        if (res && res.resultCode == 0) {
          this.upperArr.push(...changItemArr);
          // console.log("iinumber", this.downArr);

          this.downIndexArr.sort(function(x, y) {
            return y - x;
          });
          // console.log("this.downIndexArr:", this.downIndexArr);
          this.downIndexArr.forEach(ele => {
            //console.log("iinumber", this.downArr[ele]);
            this.downArr[ele].inumber = this.newModifySDish.tnumber;
            this.newMyOrderInfo.orders.forEach(element => {
              if (element.id == this.downArr[ele].id) {
                Object.assign(element, this.downArr[ele]);
              }
            });
            this.$store.commit("submiMyOrderInfo", this.newMyOrderInfo);
            this.downArr.splice(ele, 1);
          });
          this.downIndexArr = [];
        }
      } else {
        this.upperArr.push(...changItemArr);
        this.downIndexArr.sort(function(x, y) {
          return y - x;
        });
        //console.log("this.downIndexArr:", this.downIndexArr);
        this.downIndexArr.forEach(ele => {
          this.downArr.splice(ele, 1);
        });

        for (let i = this.newMyOrderInfo.orders.length - 1; i >= 0; i--) {
          changItemArr.forEach(ele => {
            if (
              this.newMyOrderInfo.orders[i].pccode == ele.pccode &&
              this.newMyOrderInfo.orders[i].sortCode == ele.sortCode &&
              this.newMyOrderInfo.orders[i].pluCode == ele.pluCode
            ) {
              this.newMyOrderInfo.orders.splice(i, 1);
            }
          });
        }
        this.newMyOrderInfo.orders.forEach(ele => {
          if (
            ele.pccode == this.newModifySDish.pccode &&
            ele.sortCode == this.newModifySDish.sortCode &&
            ele.pluCode == this.newModifySDish.pluCode
          ) {
            ele.pluStds.push(...changItemArr);
          }
        });
        this.newModifySDish.pluStds.push(...changItemArr);

        this.downIndexArr = [];
        this.$store.commit("changeTempTable", {
          type: "turnIn",
          item: this.newMyOrderInfo.orders
        });
        this.$store.commit("submiMyOrderInfo", this.newMyOrderInfo);
        this.$store.commit("submitModifySDish", this.newModifySDish);
      }
    },
    upperSelectedFun(item, index) {
      if (item.selected) {
        this.upperIndexArr.splice(index, 1);
      } else {
        this.upperIndexArr.push(index);
      }

      item.selected = !item.selected;
    },
    downSelectedFun(item, index) {
      if (item.selected) {
        this.downIndexArr.splice(index, 1);
      } else {
        this.downIndexArr.push(index);
      }
      item.selected = !item.selected;
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>
<style lang="less" scoped>
.container-packageOperation {
  padding-top: 46px;
  .container-nav {
    background: #393c41;
    padding-bottom: 9px;
    .content-nav {
      background: rgba(151, 161, 193, 0.3);
      padding: 10px 15px;
      .item {
        .item-span {
          text-align: left;
          width: 48%;
          display: inline-block;
          font-size: 14px;
          color: #ffffff;
          box-sizing: border-box;
          .amount {
            color: #caaf61;
          }
        }
      }
    }
  }
  .container-main {
    .content {
      padding-bottom: 20px;
      margin-bottom: 10px;
      background-color: #fff;
      .title {
        background-color: #fff;
        padding: 15px 0;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #c6af6c;
        font-size: 14px;
        .title-text {
          margin: 0 5px;
        }
        .title-left,
        .title-right {
          width: 24px;
          height: 1px;
          opacity: 0.4;
          background-color: #c6af6c;
          //box-shadow: inset 0 0 0 0 #c6af6c;
        }
      }
      .list {
        padding-left: 15px;

        .item {
          height: 50px;
          border-bottom: 1px solid #ddd;
          font-size: 14px;
          color: #000000;
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding-right: 15px;
          .iconfont {
            font-size: 18px;
          }
        }
        .activeItem {
          color: #c6af6c;
        }
      }
      .btn {
        background: #c6af6c;
        border-radius: 5px;
        width: 130px;
        height: 30px;
        line-height: 30px;
        color: #fff;
        margin: 20px auto 0;
      }
    }
  }
}
</style>



